/* Bootstrap Break points
// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) { ... }

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) { ... }

// Large devices (desktops, 992px and up)
@media (min-width: 992px) { ... }

// X-Large devices (large desktops, 1200px and up)
@media (min-width: 1200px) { ... }

// XX-Large devices (larger desktops, 1400px and up)
@media (min-width: 1400px) { ... }
*/

body {
	font-family: 'Lato', sans-serif;
}

/*start-style:: utility classes for Font */
.title-font {
	font-family: 'Roboto', sans-serif;
}

.text-font {
	font-family: 'Lato', sans-serif;
}
/*end-style::*/

/*start-style:: utility classes for  background  */
.bgc-light {
	background: rgb(231, 244, 246);
}

.bgc-dark {
	background: rgba(13, 110, 253, 0.3);
}

.bg-img {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: scroll;
}
/*end-style::*/

/*start-style:: style global elemnts  */
textarea {
	resize: none;
}

button:disabled {
	cursor: not-allowed;
}

iframe {
	border: none;
}
/*end-style::*/

/*start-style:: define custom classes */
.fit-content {
	width: fit-content;
}

.pointer-cursor {
	cursor: pointer;
}
/*end-style::*/

/*start-style:: Customize Bootstrap Navbar */
.navbar {
	z-index: 10000;
}

.navbar-light .navbar-nav .nav-link {
	text-transform: capitalize;
	position: relative;
	color: rgba(13, 110, 253, 0.5);
	transition: all 0.3s ease;
	text-align: center;
}

.navbar-light .navbar-nav .nav-link:hover,
.navbar-light .navbar-nav .nav-link.active {
	background-color: #0d6efd;
	color: white;
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) {
	.navbar-light .navbar-nav .nav-link:hover,
	.navbar-light .navbar-nav .nav-link.active {
		background-color: rgb(248, 249, 250);
		color: rgba(13, 110, 253, 1);
	}

	.navbar-light .navbar-nav .nav-link::after {
		content: '';
		display: block;
		position: absolute;
		background: transparent;
		top: 90%;
		left: 0;
		width: 100%;
		height: 2px;
		transition: background-color 0.3s ease;
	}

	.navbar-light .navbar-nav .nav-link:hover::after,
	.navbar-light .navbar-nav .nav-link.active::after {
		background-color: #0d6efd;
	}
}

.navbar-light .navbar-toggler {
	color: rgba(13, 110, 253, 0.55);
	border-color: rgba(13, 110, 253, 0.1);
}

/* Large devices (desktops, 992px and up)*/
@media (max-width: 991.9px) {
	.dropdown-menu-end[data-bs-popper] {
		right: auto;
		left: 0;
	}
}
/*end-style::*/

/*start-style:: Header Component */
.sec--hero {
	background-image: url('../../assets/images/header/header.jpg');

	height: calc(100vh - 76px);
}

@media (min-width: 992px) {
	.sec--hero {
		height: calc(100vh - 116px);
	}
}

.sec--hero__content {
	top: 20%;
}

/*end-style::*/

/*start-style:: main-title component */
.main-title {
	color: #212529;
	font-family: 'Roboto', sans-serif;
	position: relative;
}

.main-title::before {
	content: '';
	position: absolute;
	border-radius: 2px;
	top: 26%;
	left: -30px;
	transform: skewX(-15deg);
	width: 18px;
	height: 18px;
	background: #0d6efd;
	display: inline-block;
}

.main-title::after {
	content: '';
	position: absolute;
	border-radius: 2px;
	top: 100%;
	left: 50%;
	transform: translateX(-50%);
	height: 4px;
	width: 50%;
	background: #0d6efd;
}
/*end-style::*/

/*start-style:: start track items  in tracks section*/
.track__image__container {
	width: 55px;
	height: 55px;
}

.track__image {
	width: 30px;
	height: 30px;
}
/*end-style::*/

/*start-style:: Slider Styles in gallery section*/
.carousel-control-prev,
.carousel-control-next {
	width: auto;
	background: #0d6efd;
	fill: white;
	padding: 5px;
	border-radius: 5px;

	top: -55px;
	bottom: auto;
}

.carousel-control-prev {
	left: 0;
}

.carousel-control-next {
	right: 0;
}
/*end-style::*/

/*start-style::  location map in contact section */
.contact__map {
	min-height: 450px;
}
/*end-style::*/

/*start-style:: style sections*/
.sec {
	padding: 35px 0;
}

/*Large devices (desktops, 992px and up)*/
@media (min-width: 992px) {
	.sec {
		padding: 60px 0;
	}
}

.sec--tracks {
	background-image: linear-gradient(
		45deg,
		rgba(231, 244, 246, 0.7),
		rgba(101, 199, 247, 0.7),
		rgba(13, 110, 253, 0.7)
	);
}

.sec--team {
	background-image: linear-gradient(
			to right,
			rgba(231, 244, 246, 0.3),
			rgba(101, 199, 247, 0.3),
			rgba(13, 110, 253, 0.3)
		),
		url('../../assets/images/bg/team-bg.jpg');
}

.sec--contact {
	background-image: linear-gradient(
			to top,
			rgba(231, 244, 246, 0.95),
			rgba(101, 199, 247, 0.95),
			rgba(13, 110, 253, 0.4)
		),
		url('../../assets/images/bg/contact-bg.jpg');
}
/*end-style::*/

/*start-style::  footer*/
footer {
	color: #f1e9e5;
}
/*end-style::*/

/*start-style::  social-icons List in  footer */
.social-icons__item {
	margin-right: 10px;
}
.social-icons__item:last-child {
	margin-right: 0;
}

.social-icons__item__link {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	text-decoration: none;
	background-color: #fff;
}

.social-icons__item__link:hover {
	background-color: #0d6efd;
}

.social-icons__item__link__icon {
	font-size: 18px;
	font-weight: bold;
	transition: transform 0.7s ease;
}

.social-icons__item__link:hover .social-icons__item__link__icon {
	transform: rotate(360deg);
	color: white;
}
/*end-style::*/

/*start-style::  customize scroll bar  */
/* https://css-tricks.com/the-current-state-of-styling-scrollbars/ */
html {
	--scrollbarBG: linear-gradient(90deg, #434343, #434343 1px, #111 0, #111);
	--thumbBG: #434343;
}

body::-webkit-scrollbar {
	width: 13px;
	height: 16px;
}

body {
	scrollbar-width: thin;
	scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}

body::-webkit-scrollbar-track {
	background: var(--scrollbarBG);
}

body::-webkit-scrollbar-thumb {
	background-color: var(--thumbBG);
	border-radius: 16px;
	border: 3px solid var(--scrollbarBG);
	box-shadow: inset 2px 2px 2px rgba(255, 255, 255, 0.25),
		inset -2px -2px 2px rgba(0, 0, 0, 0.25);
}
/*end-style::*/

